import React, { Component } from 'react';
import { Pagination, PaginationItem, PaginationLink,Input} from 'reactstrap';


const inputNumberStyle = {
  width: "100px",
  fontWeight:"bold"
};

class PaginationFooter extends Component {

  constructor(props) {
    super(props);
    this.handleChangePage = this.handleChangePage.bind(this);
  }


  handleChangePage = (e) => {
    let nextPage = e.currentTarget.value > this.props.lastPage ? this.props.lastPage : e.currentTarget.value;
    this.props.handleChangePage(nextPage);
  }

  render(){

      var pagiantion= null;


      const content = []


      //GENERA TUTTE LE PAGINE

      // for (var i = 1 ; i < this.props.lastPage; i++) {
      //    content.push(<PaginationItem
      //       key={i}
      //       active={this.props.currentPage === i ? true : false  }>
      //       <PaginationLink
      //          value={i}
      //          onClick={this.handleChangePage}
      //          >
      //         {i}
      //       </PaginationLink>
      //     </PaginationItem>)
      // }


      //GENERO 5 Elementi


      for (var i = 2 ; i > 0; i --){
        if(this.props.currentPage  -i > 1){
          content.push(<PaginationItem
             key={this.props.currentPage -i}
             active={this.props.currentPage === i ? true : false  }>
             <PaginationLink
                value={this.props.currentPage -i}
                onClick={this.handleChangePage}
                >
               {this.props.currentPage -i}
             </PaginationLink>
           </PaginationItem>)
       }



      }

      content.push(<PaginationItem
         key={i}
         active={this.props.currentPage === i ? true : false  }>
           <Input  value={this.props.currentPage} type="number" className="page-link" style={{width: "100px",fontWeight:"bold"}} onChange={this.handleChangePage}/>
       </PaginationItem>)


       for (var i = 1 ; i <3 ; i ++) {
         if(this.props.currentPage + i < this.props.lastPage){
           content.push(<PaginationItem
              key={this.props.currentPage +i}
              active={this.props.currentPage === i ? true : false  }>
              <PaginationLink
                 value={this.props.currentPage +i }
                 onClick={this.handleChangePage}
                 >
                {this.props.currentPage +i}
              </PaginationLink>
            </PaginationItem>)
        }
       }



    return(
      <Pagination aria-label="Page navigation example">
        <PaginationItem  disabled={this.props.currentPage === 1 ? true :  false } value="1"  key="first" onClick={this.handleChangePage}>
          <PaginationLink  >1</PaginationLink>
        </PaginationItem>
        <PaginationItem disabled={this.props.currentPage === 1 ? true :  false }  value={this.props.currentPage - 1}   onClick={this.handleChangePage} >
          <PaginationLink   key="prev" previous />
        </PaginationItem>
          {content}
        <PaginationItem  disabled={this.props.currentPage === this.props.lastPage ?  true : false  } value={this.props.currentPage + 1} onClick={this.handleChangePage}>
          <PaginationLink   key="next" next  />
        </PaginationItem>
        <PaginationItem  disabled={this.props.currentPage === this.props.lastPage ?  true : false  } value={this.props.lastPage}  onClick={this.handleChangePage}>
          <PaginationLink  key="last" >{this.props.lastPage}</PaginationLink>
        </PaginationItem>
      </Pagination>

    );
  }

}

export default PaginationFooter
