import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import * as actions from '../store/actions/index';
import Page from 'components/Page';
import { Card, CardHeader, CardBody, Row, Col, Form, Button, Spinner, Table} from 'reactstrap';
import { Bar } from 'react-chartjs-2';
import PaginationFooter  from '../components/Pagination/PaginationFooter';
import PaginationHeader from '../components/Pagination/PaginationHeader';

import NotificationSystem from 'react-notification-system-redux';
import { NOTIFICATION_SYSTEM_STYLE, BAR_CHART_OPTIONS } from 'utils/constants';
import { getColor } from 'utils/colors';
import DateRange from '../components/Filter/DateRange'
import ProductFilter from '../components/Filter/Product'
import AffiliateFilter from '../components/Filter/Affiliate'
import RowNoData from '../components/RowNoData';
import NumberFormat from 'react-number-format';
import formatDate from '../utils/formatDate'
import BasePage from './BasePage';


const initChartDataset = () => {
    return [
        {
            label: 'Incasso anno corrente',
            borderColor: getColor('primary'),
            backgroundColor: getColor('primary'),
            data: [],
        },
        {
            label: 'Incasso anno precedente',
            borderColor: getColor('secondary'),
            backgroundColor: getColor('secondary'),
            data: [],
        }
    ]
}

const chartOptions = () => {
    return {
        ...BAR_CHART_OPTIONS
    }
}

const date = new Date()

class RevenueComparisionPage extends BasePage {
    state = {
        filters: {
            from: formatDate(new Date(date.getFullYear() -1, date.getMonth(), date.getDate())),
            to: formatDate(new Date(date.getFullYear(), date.getMonth(), date.getDate())),
            product_group_id: null,
            product_department_id: null,
            product_id: null,
            product_detail_group_id:null,
            product_detail_department_id:null


        },
        groupBy: "groups",
        groupByRelation: "group",
        groupByFilterName: "product_group_id",
        searchClicked: false,
        touched: false,
        paginateOptions:[10,20,30],
        currentPage:null,
        paginateElements:10


    }

    handleChangePage = (e) => {

      this.setState({ currentPage: e }, () => {
          this.handleSubmit()
      });

    }

    handleChangePagination = (e) => {

      this.setState({ paginateElements: e }, () => {
          this.handleSubmit()
      });

    }

    handleFilterChange = (value, name) => {
        const { filters } = this.state
        filters[name] = value
        this.setState({
            filters
        })
        switch (name) {
            case "product_group_id":
                filters["product_detail_group_id"] = value
                this.setState({
                    filters
                })
                break;
            case "product_department_id":
                filters["product_detail_department_id"] = value
                this.setState({
                    filters
                })
                break;
            case "product_id":
                filters["product_id"] = value
                this.setState({
                    filters
                })
                break;
            default:
                break;
        }


        if (value === null) {
            switch (name) {
                case "product_group_id":
                    this.changeGroupBy("")
                    break;
                case "product_department_id":
                    this.changeGroupBy("groups")
                    break;
                default:
                    break;
            }
        }
    }

    handleSubmit = () => {
        const { searchClicked } = this.state
        this.setState({
            searchClicked: !searchClicked,
            touched: true
        })
        this.props.onLoadStats(this.state.currentPage, this.state.paginateElements, this.state.filters, ['affiliate', 'product', 'group', 'department'], null)
    }

    handleDetailClick = (value, name, current) => {
        this.handleFilterChange(value, name)
        this.handleSubmit()
        this.changeGroupBy(current)
    }

    changeGroupBy = (value) => {

        let { groupBy, groupByRelation, groupByFilterName } = this.state
        switch (value) {
            case "groups":
                groupBy = "departments"
                groupByRelation = "department"
                groupByFilterName = "product_department_id"
                break;
            case "departments":
                groupBy = "families"
                groupByRelation = "family"
                groupByFilterName = "product_id"
                break;
            default:
                groupBy = "groups"
                groupByRelation = "group"
                groupByFilterName = "product_group_id"
                break;
        }
        this.setState({
            groupBy,
            groupByRelation,
            groupByFilterName
        })
    }

    handleReset = () => {
        const { filters } = this.state
        const names = Object.keys(filters)
        names.forEach(name => {
            filters[name] = null
        })
        this.setState({
            filters
        })
        this.changeGroupBy("")
    }

    render() {
        let rows = <RowNoData colspan={7} />
        let footer = null
        let charts = null
        let pagination = null
        const { groupBy, groupByRelation, groupByFilterName, touched } = this.state
        const { stats, loading, error, searchClicked } = this.props
        let chartData = {
            labels: [],
            datasets: initChartDataset()
        }
        if (this.props.loading) {
            charts = <Card><CardBody><Spinner color="primary" /></CardBody></Card>
        }
        if (loading === false && error === false && stats !== null && touched === true) {
            let data = {}
            let amountTotal = 0
            let amountTotalPrevYear = 0


            rows = this.props.stats.data.map( object => {

                amountTotal += object.amount
                amountTotalPrevYear += object.amount_prev_year

                chartData.labels.push(object.description)
                chartData.datasets[0].data.push(object.amount.toFixed(2))
                chartData.datasets[1].data.push(object.amount_prev_year.toFixed(2))
                return <tr key={object.id}>
                    <td className="text-left"><Button
                        color="link"
                        onClick={() => this.handleDetailClick(object.id, groupByFilterName, groupBy)}
                        >{object.description}</Button></td>
                    <td className="text-right"><NumberFormat value={object.amount} displayType={'text'} thousandSeparator={'.'} decimalSeparator={','} decimalScale={2} fixedDecimalScale={true} renderText={value => <div>{value}</div>} /></td>
                    <td className="text-right"> {(object.amount * 100 / amountTotal).toFixed(2)} %</td>
                    <td className="text-right"><NumberFormat value={object.amount_prev_year} displayType={'text'} thousandSeparator={'.'} decimalSeparator={','} decimalScale={2} fixedDecimalScale={true} renderText={value => <div>{value}</div>} /></td>
                    <td className="text-right">{(object.amount_prev_year * 100 / amountTotalPrevYear).toFixed(2)} %</td>
                    <td className="text-right"><NumberFormat value={(object.amount - object.amount_prev_year)} displayType={'text'} thousandSeparator={'.'} decimalSeparator={','} decimalScale={2} fixedDecimalScale={true} renderText={value => <div>{value}</div>} /></td>
                    <td className="text-right">{((object.amount - object.amount_prev_year) * 100 / object.amount).toFixed(2)} %</td>
                </tr>
            })
            footer = <tr>
                <th>Totale incassi:</th>
                <td className="text-right"><NumberFormat value={amountTotal} displayType={'text'} thousandSeparator={'.'} decimalSeparator={','} decimalScale={2} fixedDecimalScale={true} renderText={value => <div>{value}</div>} /></td>
                <td className="text-right">100 %</td>
                <td className="text-right"><NumberFormat value={amountTotalPrevYear} displayType={'text'} thousandSeparator={'.'} decimalSeparator={','} decimalScale={2} fixedDecimalScale={true} renderText={value => <div>{value}</div>} /></td>
                <td className="text-right">100 %</td>
                <td className="text-right"><NumberFormat value={(amountTotal - amountTotalPrevYear)} displayType={'text'} thousandSeparator={'.'} decimalSeparator={','} decimalScale={2} fixedDecimalScale={true} renderText={value => <div>{value}</div>} /></td>
                <td className="text-right">{((amountTotal - amountTotalPrevYear) * 100 / amountTotal).toFixed(2)} %</td>
            </tr>

            charts = <Row>
                <Col>
                    <Card>
                        <CardHeader>Paragone incassi</CardHeader>
                        <CardBody>
                            <Bar data={chartData} options={chartOptions()} />
                        </CardBody>
                    </Card>
                </Col>
            </Row>

            pagination = <PaginationFooter currentPage={this.props.stats.current_page} lastPage={this.props.stats.last_page}  handleChangePage={this.handleChangePage.bind(this)}/>
        }
        return (
            <Page title="Vendite prodotti" breadcrumbs={[{ name: 'vendite prodotti', active: true }]}>
                <Row>
                    <Col>
                        <Card className="mb-3">
                            <CardHeader>
                                Filtri
                            </CardHeader>
                            <CardBody>
                                <Form onSubmit={this.handleSubmit}>
                                    <DateRange onChange={this.handleFilterChange.bind(this)}  fromStartDate={this.state.filters.from} toStartDate={this.state.filters.to}/>
                                    <ProductFilter onChange={this.handleFilterChange.bind(this)} />
                                    <Row>
                                        <AffiliateFilter onChange={this.handleFilterChange.bind(this)} />
                                    </Row>
                                    <Row>
                                        <Col>
                                            <Button
                                                color="primary"
                                                disabled={this.props.loading && this.state.searchClicked}
                                                onClick={this.handleSubmit}>
                                                Cerca
                                            </Button>&nbsp;
                                            <Button
                                                color="secondary"
                                                disabled={this.props.loading && this.state.searchClicked}
                                                onClick={this.handleReset}>
                                                Reset
                                            </Button>
                                        </Col>
                                    </Row>
                                </Form>
                            </CardBody>
                        </Card>
                    </Col>
                </Row>
                { charts }
                <Row>
                    <Col>
                        <Card>
                            <CardHeader>
                                <Row>
                                    <Col  sm="12" md={{ size: 3, offset: 9}}>
                                      <PaginationHeader options={this.state.paginateOptions} handleChangePagination={this.handleChangePagination}/>
                                    </Col>
                                </Row>
                            </CardHeader>
                            <CardBody>
                                <Table responsive>

                                    <thead>
                                        <tr>
                                            <th className="text-left">Categoria</th>
                                            <th className="text-right">Anno corrente €</th>
                                            <th className="text-right">Incidenza %</th>
                                            <th className="text-right">Anno precedente €</th>
                                            <th className="text-right">Incidenza %</th>
                                            <th className="text-right">Diff. val €</th>
                                            <th className="text-right">Diff %</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {rows}
                                    </tbody>
                                    <tfoot>
                                        {footer}

                                    </tfoot>
                                </Table>
                                {pagination}
                            </CardBody>
                        </Card>
                    </Col>
                </Row>
                <NotificationSystem
                    dismissible={false}
                    notifications={this.props.notifications}
                    style={NOTIFICATION_SYSTEM_STYLE}
                />
            </Page>
        )
    }
}

RevenueComparisionPage.contextTypes = {
    store: PropTypes.object
};

RevenueComparisionPage.propTypes = {
    notifications: PropTypes.array,
    stats: PropTypes.object,
    affiliates: PropTypes.object,
    products: PropTypes.object,
    groups: PropTypes.object,
    departments: PropTypes.object,
    families: PropTypes.object,
    loading: PropTypes.bool,
    error: PropTypes.bool,
    pdfAvaiable: PropTypes.bool,
};

const mapStateToProps = state => {
    return {
        stats: state.statistics.history,
        affiliates: state.statistics.affiliates,
        products: state.statistics.products,
        groups: state.statistics.groups,
        departments: state.statistics.departments,
        failies: state.statistics.families,
        loading: state.statistics.loading,
        error: state.statistics.error,
        pdfAvaiable: state.statistics.pdfAvaiable,
        notifications: state.notifications,



    };
};

const mapDispatchToProps = dispatch => {
    return {
        onLoadStats: (page, perPage, filters, include, order) => dispatch(actions.loadHistoricalStat(page, perPage, filters, include, order)),
    };
};
export default connect(mapStateToProps, mapDispatchToProps)(RevenueComparisionPage);
