import React, { useState } from 'react';

import { Col, Row, FormGroup, Label } from 'reactstrap';
import AsyncSelect from 'react-select/async';

import api from '../../api'


const Product = (props) => {
    const [groupId, setGroupId] = useState(
        null
    );

    const [departmentId, setDepartmentId] = useState(
        null
    );

    const promiseProductGroupOptions = inputValue => {
        const url = `product-groups${inputValue ? '?filter[text]=' + inputValue : ''}`
        return api.get(url)
            .then(response => {
                const options = response.data.data.map(group => ({ value: group.id, label: group.attributes.description }));
                return options;
            })
            .catch(err => {
                console.log('some error', err);
            });
    };

    const promiseProductDepartmentOptions = inputValue => {
        const url = `product-departments${inputValue ? '?filter[text]=' + inputValue : ''}${groupId ? '&filter[group_id]=' + groupId : ''}`
        return api.get(url)
            .then(response => {
                const options = response.data.data.map(customer => ({ value: customer.attributes.code, label: customer.attributes.description }));
                return options;
            })
            .catch(err => {
                console.log('some error', err);
            });
    };

    const promiseProductOptions = inputValue => {
        if (inputValue.length > 3) {
            const url = `products${inputValue ? '?filter[text]=' + inputValue : ''}${groupId ? '&filter[group_id]=' + groupId : ''}${departmentId ? '&filter[department_id]=' + departmentId : ''}`
            return api.get(url)
                .then(response => {
                    const options = response.data.data.map(customer => ({ value: customer.id, label: customer.attributes.description }));
                    return options;
                })
                .catch(err => {
                    console.log('some error', err);
                });
        }
    };

    const handleChangeGroup = (event) => {
        const value = event ? event.value : null
        props.onChange(value, 'product_group_id')
        setGroupId(value)
    }

    const handleChangeDepartment = (event) => {
        const value = event ? event.value : null
        props.onChange(value, 'product_department_id')
        setDepartmentId(value)
    }
    return (
        <Row>
            <Col md={4}>
                <FormGroup>
                    <Label for="exampleUrl">Gruppo Prodotto</Label>
                    <AsyncSelect
                        isClearable
                        cacheOptions
                        loadOptions={promiseProductGroupOptions}
                        defaultOptions
                        onChange={(event) => handleChangeGroup(event)}
                    />
                </FormGroup>
            </Col>
            <Col md={4}>
                <FormGroup>
                    <Label for="exampleUrl">Reparto prodotto</Label>
                    <AsyncSelect
                        isClearable
                        loadOptions={promiseProductDepartmentOptions}
                        defaultOptions
                        onChange={(event) => handleChangeDepartment(event)}
                    />
                </FormGroup>
            </Col>
            <Col md={4}>
                <FormGroup>
                    <Label for="exampleUrl">Prodotto</Label>
                    <AsyncSelect
                        isClearable
                        loadOptions={promiseProductOptions}
                        defaultOptions
                        onChange={(event) => props.onChange(event.value, 'product_id')}
                    />
                </FormGroup>
            </Col>
        </Row>
    );
};

export default Product;
