import React, { Component } from 'react';
import { FormGroup, Input} from 'reactstrap';




class PaginationHeader extends Component {


  handleChangePagination = (e) => {
    this.props.handleChangePagination(e.currentTarget.value);
  }

  render(){

    const content = []

    for (var i = 0 ; i < this.props.options.length; i++) {
       content.push(<option key={this.props.options[i]} value={this.props.options[i]}>{this.props.options[i]}</option>)
    }

  return (  <FormGroup>
      <label>Elementi per pagina:</label>
      <Input type="select" name="select" id="per_page" onChange={this.handleChangePagination}>
        {content}
      </Input>
    </FormGroup>

    );
  }

}

export default PaginationHeader
